import React from 'react';

class SignIn extends React.Component {

	constructor(props) {

		super(props);
		this.state = {

			email: '',
			password: ''

		};

	}

	onEmailChange = (event) => {

		this.setState({email: event.target.value});

	};

	onPasswordChange = (event) => {

		this.setState({password: event.target.value});

	};

	onSubmitSignIn = () => {

		fetch('https://fudy-api.herokuapp.com/signin', {
			method: 'post',
			headers: {
				'Content-Type': 'application/json'
			},
			body: JSON.stringify({
				email: this.state.email,
				password: this.state.password
			})
		}).then(res => res.json())
		.then(user => {

			if (user.id) {

				this.props.loadUser(user);
				this.props.onPageChange('home');
		
			}

		}).catch(console.log);

	};

	render() {

		return (
			
			<article className="br2 ba dark-gray b--black-10 mv4 w-100 w-50-m w-25-l mw6 center shadow-5">
				<main className="pa4 black-80">
				  <div className="measure">
				    <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
				      <legend className="f1 fw6 ph0 mh0">Sign In</legend>
				      <div className="mt3">
				        <label className="db fw6 lh-copy f5" htmlFor="email-address">Email</label>
				        <input onChange={this.onEmailChange} className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="email" name="email-address"  id="email-address" />
				      </div>
				      <div className="mt3">
				        <label className="db fw6 lh-copy f5" htmlFor="password">Password</label>
				        <input onChange={this.onPasswordChange} className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="password" name="password"  id="password" />
				      </div>
				    </fieldset>
				    <div className="mt3">
				      <input onClick={this.onSubmitSignIn} className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib" type="submit" value="Sign in" />
				    </div>
				    <div className="lh-copy mt3">
				      <p onClick={() => this.props.onPageChange('signup')} href="#0" className="f6 link dim black db">Register</p>
				    </div>
				  </div>
				</main>
			</article>

		)

	}

}

export default SignIn;


